<template>
	<div class="anli">
		<div class="banner">
			<div class="font-69">优秀项目案例</div>
			<div class="font-42">找到成功背后的关键因素，启发您的商业思维</div>
		</div>
		<div class="tag">
			<div class="tag-key">
				<span>行业：</span>
				<div class="tag-item" v-for="(key, index) in tabKeyArr" :key="parseInt(index)">
					<a-checkable-tag
						:key="key.name"
						:checked="selectedTags.indexOf(key.name) > -1"
						@change="checked => handleChange(key.name, checked)"
					>
						{{ key.name }}
					</a-checkable-tag>
				</div>
			</div>
			<div class="tag-value">
				<div class="tag-item" v-for="(tag, index) in tabValueArr" :key="index">
					<a-checkable-tag
						:key="tag"
						:checked="selectedValueTags.indexOf(tag) > -1"
						@change="checked => handleValueChange(tag, checked)"
					>
						{{ tag }}
					</a-checkable-tag>
				</div>
			</div>
		</div>
		<div class="bp">
			<div class="bp-main">
				<a-row>
					<a-col class="bp-item" :span="8"  v-for="(item, index) in bgArr" :key="index" @click="switchDetail()">
						<img :src="item.img" alt="">
						<span>{{item.title}}</span>
					</a-col>
				</a-row>
			</div>
			<a-pagination
				v-model="current"
				:page-size-options="pageSizeOptions"
				:total="total"
				show-size-changer
				:page-size="pageSize"
				@showSizeChange="onShowSizeChange"
			>
				<template slot="buildOptionText" slot-scope="props">
					<span v-if="props.value !== '50'">{{ props.value }}条/页</span>
					<span v-if="props.value === '50'">全部</span>
				</template>
			</a-pagination>
		</div>
		<div class="tip">
			<img :src="require('@/assets/anli/tip-icon.png')" alt="">
			<span>温馨提示：所有文档均来源于公开渠道，不得用于商业用途，所有权归属原公司或个人，如有侵权请联系我们处理</span>
		</div>
	</div>
</template>

<script>
import axios from 'axios';
export default {
	name: 'Index',
	data () {
		return {
			tabKeyArr: ['互联网', '移动互联网', '医疗健康', '环保能源', '制造业', '本地生活', '教育', '金融', '文化娱乐', '硬件', '农业', '游戏'],
			tabValueArr: ['互联网', '移动互联网', '医疗健康', '环保能源', '制造业', '本地生活', '教育', '金融', '文化娱乐', '硬件', '农业', '游戏'],
			selectedTags: [],
			selectedValueTags: [],
			// 
			pageSizeOptions: ['10', '20', '30', '40', '50'],
			current: 1,
			pageSize: 20,
			total: 500,
			// 
			bgArr: [{
				img: require('@/assets/logo.png'),
				title: '商业计划书标题商业计划书标题商业计划书标题'
			}, {
				img: require('@/assets/logo.png'),
				title: '商业计划书标题商业计划书标题商业计划书标题'
			}, {
				img: require('@/assets/logo.png'),
				title: '商业计划书标题商业计划书标题商业计划书标题'
			}, {
				img: require('@/assets/logo.png'),
				title: '商业计划书标题商业计划书标题商业计划书标题'
			}, {
				img: require('@/assets/logo.png'),
				title: '商业计划书标题商业计划书标题商业计划书标题'
			}, {
				img: require('@/assets/logo.png'),
				title: '商业计划书标题商业计划书标题商业计划书标题'
			}, {
				img: require('@/assets/logo.png'),
				title: '商业计划书标题商业计划书标题商业计划书标题'
			}, {
				img: require('@/assets/logo.png'),
				title: '商业计划书标题商业计划书标题商业计划书标题'
			}, {
				img: require('@/assets/logo.png'),
				title: '商业计划书标题商业计划书标题商业计划书标题'
			}, {
				img: require('@/assets/logo.png'),
				title: '商业计划书标题商业计划书标题商业计划书标题'
			}, {
				img: require('@/assets/logo.png'),
				title: '商业计划书标题商业计划书标题商业计划书标题'
			}, ]
		}
	},
	methods: {
		handleChange(tag, checked) {
			const { selectedTags } = this;
			const nextSelectedTags = checked
				? [...selectedTags, tag]
				: selectedTags.filter(t => t !== tag);
			console.log('You are interested in: ', nextSelectedTags);
			this.selectedTags = nextSelectedTags;
		},
		handleValueChange(tag, checked) {
			const { selectedValueTags } = this;
			const nextSelectedTags = checked
				? [...selectedValueTags, tag]
				: selectedValueTags.filter(t => t !== tag);
			console.log('You are interested in: ', nextSelectedTags);
			this.selectedValueTags = nextSelectedTags;
		},
		onShowSizeChange(current, pageSize) {
			this.pageSize = pageSize;
		},
		// 跳转到详情页
		switchDetail() {
			this.$router.push({name: 'Detail', params: {title: '案例详情页'}})
		},
		// 获取行业信息
		getHangyeData() {
			axios.get(`https://www.youjunming.cn/index.php/v1/domain`, {
				params: {
					
				}
			})
			.then((res) => {
				this.tabKeyArr = res.data.data;
			})
		}
	},
	mounted () {
		this.getHangyeData();
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
	.anli {
		text-align: left;
		margin-top: 64px;
	}
	.banner {
		width: 100%;
		height: 300px;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		background-color: #cccccc;
		color: #ffffff;
		padding-bottom: 15px;
		.font-69 {
			font-size: 69px;
		}
		.font-42 {
			font-size: 42px;
		}
	}
	.ant-tag-checkable-checked {
		color: #FF0042;
		background-color: #ffffff;
		font-weight: bold;
	}
	.ant-tag {
		font-size: 19px !important;
		&:hover {
			color: #FF0042;
		}
	}
	.tag {
		width: 90%;
		margin: auto;
		margin-top: 20px;
		box-shadow:#e1e1e1 0px 0px 10px;
		font-size: 17px;
		padding: 15px 25px;
		line-height: 35px;
		&-key {
			span {
				display: inline-block;
				padding-left: 7px;
				font-size: 19px;
			}
		}
		&-item {
			display: inline-block;
		}
		&-value {
			
		}
	}
	.bp {
		width: 90%;
		display: flex;
		flex-direction: column;
		align-items: center;
		margin: auto;
		margin-top: 25px;
		&-item {
			text-align: center;
			cursor: pointer;
			img {
				width: 90%;
			}
			span {
				display: block;
				width: 85%;
				text-align: left;
				margin: auto;
				margin-top: 15px;
				margin-bottom: 35px;
				font-size: 25px;
			}
		}
	}
	.tip {
		display: flex;
		align-items: center;
		justify-content: center;
		margin: 40px 0;
		font-size: 25px;
		color: #C4C4C4;
		img {
			width: 32px;
		}
		span {
			margin-left: 15px;
		}
	}
</style>



